@import '../core/variables';

.contact {
  background-color: white;
  padding: 2rem;
  width: 100%;
  height: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  @media (min-width: 1200px) {
    display: none;
  }
  &__title {
    text-transform: uppercase;
    font-family: 'Neucha', cursive;
    font-size: 1.7rem;
    text-align: center;
    margin-top: 1rem;
    @media (min-width: 768px) {
      font-size: 2.5rem;
      padding-top: 2rem;
    }
  }
  &__icon {
    width: 40px;
    margin: 2rem 2.1rem;
    align-items: center;
    // @media (min-width: 768px) {
    //   width: 45px;
    //   margin-top: 0.5rem;
    //   padding-bottom: 2rem;
    // }
  }
}
